.todoListMain .header input {
    padding: 20px;
    font-size:26px;
    border:4px solid rgb(39, 37, 37);
    width:265px;
    
}
.todoListMain .header button {
    padding: 20px;
    font-size: 26px;
    margin:10px;
    margin-right: 0px;
    background-color: rgb(75, 46, 238);
    color:rgb(17, 11, 11);
    border:2px solid #0066ff;
}
.todoListMain .header button:hover {
    background-color:#003399;
    border:2px solid #003399;
    cursor:pointer;
} 
.todoListMain .theList li {
    color:rgb(184, 23, 23);
    background-color: rgb(32, 126, 189);
    padding: 15px;
    margin-bottom:15px;
    border-radius:5px;
    list-style: none;

    transition:background-color .2s ease-out;
}
ul.theList{
    padding:0;
}
.todoListMain .theList li:hover {
    background-color:pink;
    cursor:pointer;
}
#bottom { 
    position:absolute;                  
    bottom:0;                          
    left:0;
    margin-left: 4px; 
    font-style: italic;                         
} 
